






























































import { Component, Vue } from 'vue-property-decorator';
import './WalletImport.less'
import {NetworkType} from "nem2-sdk";

@Component({
    components: {},
})
export default class WalletImport extends Vue{
    currentTab = 'mnemonic'
    mnemonic = {
        mnemonic:'',
        password: '',
    }
    privateKey = {
        privateKey:'',
        password: '',
        checkPW: '',
    }
    keystore = {
        keystore:'',
        password: '',
    }
    netType = [
        {
            value:NetworkType.MIJIN_TEST,
            label:'MIJIN_TEST'
        },{
            value:NetworkType.MAIN_NET,
            label:'MAIN_NET'
        },{
            value:NetworkType.TEST_NET,
            label:'TEST_NET'
        },{
            value:NetworkType.MIJIN,
            label:'MIJIN'
        },
    ]
    changeTab (name) {
        this.currentTab = name
    }
    success (title,desc) {
        this.$Notice.success({
            title: title,
            desc:  desc?desc:''
        });
    }
    importWallet () {
        switch (this.currentTab) {
            case 'mnemonic':
                this.$store.commit('SET_WALLET_LIST',[{name:'a'}])
                this.$store.commit('SET_HAS_WALLET',true)
                this.success('成功导入钱包','')
                this.mnemonic = {
                    mnemonic:'',
                    password: '',
                }
                break;
            case 'privateKey':
                this.$store.commit('SET_WALLET_LIST',[{name:'a'}])
                this.$store.commit('SET_HAS_WALLET',true)
                this.success('成功导入钱包','')
                this.privateKey = {
                    privateKey:'',
                    password: '',
                    checkPW: '',
                }
                break;
            case 'keystore':
                this.$store.commit('SET_WALLET_LIST',[{name:'a'}])
                this.$store.commit('SET_HAS_WALLET',true)
                this.success('成功导入钱包','')
                this.keystore = {
                    keystore:'',
                    password: '',
                }
                break;
        }
    }
}
